export function zeroPadLeft(num, size = 2) {
  let str = "" + num;
  while (str.length < size) {
    str = "0" + str;
  }
  return str;
}

export function calculateDailyHours(api, events) {
  let weekStart = new Date(api.currentData.dateProfile.activeRange.start);
  weekStart = new Date(weekStart.getUTCFullYear(), weekStart.getUTCMonth(), weekStart.getUTCDate())
  let dailyHours = [0, 0, 0, 0, 0, 0, 0];
  let eventStart, eventEnd, nextDayStart, day, duration;
  if (events && events.length) {
    events.forEach(e => {
      eventStart = new Date(e.start);
      eventEnd = new Date(e.end);

      // this loop checks for events overflowing to the next date
      nextDayStart = new Date(eventStart.getUTCFullYear(), eventStart.getUTCMonth(), eventStart.getUTCDate() + 1);
      while (eventEnd.getTime() > nextDayStart.getTime()) {
        duration = (nextDayStart.getTime() - eventStart.getTime()) / 3600000;
        day = Math.floor((eventStart.getTime() - weekStart.getTime()) / 24 / 60 / 60 / 1000);
        dailyHours[day] += duration;
        eventStart = new Date(nextDayStart);
        nextDayStart.setDate(nextDayStart.getDate() + 1);
      }
      duration = (eventEnd.getTime() - eventStart.getTime()) / 3600000;
      day = Math.floor((eventStart.getTime() - weekStart.getTime()) / 24 / 60 / 60 / 1000);
      // careful not to overflow the array :)
      if (day < dailyHours.length) {
        dailyHours[day] += duration;
      }
    });
  }
  return dailyHours;
}

export function createWeekDayHourTemplate(arr) {
  let lhrs = "Hrs: ";
  let sd = document.querySelector(".fc-scrollgrid-shrink");
  let minWidth = sd && sd.clientWidth || 50;

  let table = document.createElement("table");
  table.className = "total-week-day-hour";

  let thead = document.createElement("thead");
  table.append(thead);

  let row = document.createElement("tr");
  thead.append(row);

  let firstCell = document.createElement("th");
  firstCell.className = "fc-timegrid-slot fc-timegrid-slot-label fc-scrollgrid-shrink";
  firstCell.style.width = minWidth + "px";
  row.append(firstCell);
  let shrinkFrameDiv = document.createElement("div");
  shrinkFrameDiv.className = "fc-timegrid-slot-label-frame fc-scrollgrid-shrink-frame";
  firstCell.append(shrinkFrameDiv);
  let shrinkCushionDiv = document.createElement("div");
  shrinkCushionDiv.className = "fc-timegrid-slot-label-cushion fc-scrollgrid-shrink-cushion";
  shrinkFrameDiv.append(shrinkCushionDiv);

  let days = ["mon", "tue", "wed", "thu", "fri", "sat", "sun"];
  arr.forEach((val, i) => {
    let cell = document.createElement("th");
    cell.className = "fc-day-header fc-widget-header fc-" + days[i];
    cell.append(lhrs + val);
    row.append(cell);
  });

  return table;
}