<template>
  <div class="wrapper">
    <div class="header">
      <h3 class="page-title">Work Schedules</h3>
    </div>
    <div class="content">
      <h5 class="total-week-hour">Weekly hrs scheduled: {{ weeklyHours }}</h5>
      <v-dialog
        persistent
        scrollable
        v-model="showDialog"
        max-width="640px"
        :hide-overlay="$vuetify.breakpoint.mdAndDown"
        :fullscreen="$vuetify.breakpoint.mdAndDown"
      >
        <EditShift
          title="Edit Shift"
          v-model="selectedCalendarEvent"
          @change="updateEvent"
          @deleteEvent="deleteEvent"
          @close="closeDialog"
        ></EditShift>
      </v-dialog>
      <FullCalendar id="calendar" ref="calendar" :options="calendarOptions">
        <template v-slot:eventContent="evt">
          <div class="float-right ma-1">
            <v-icon dark @click.stop="deleteEvent(evt.event)">mdi-close</v-icon>
          </div>
          <div class="float-left ma-1">
            {{ evt.timeText }}<br />
            {{ evt.event.title }}
          </div>
        </template>
      </FullCalendar>
    </div>
  </div>
</template>

<script>
import FullCalendar from "@fullcalendar/vue";
import timeGridPlugin from "@fullcalendar/timegrid";
import interactionPlugin from "@fullcalendar/interaction";
import EditShift from "./EditShift";
import CalendarService from "../services/CalendarService";
import { calculateDailyHours, createWeekDayHourTemplate } from "../utils";

export default {
  components: {
    FullCalendar,
    EditShift
  },
  data: function () {
    return {
      showDialog: false,
      allowWeekEdit: true,
      weeklyHours: 0,
      calendarOptions: {
        timeZone: "local",
        plugins: [timeGridPlugin, interactionPlugin],
        buttonText: {
          today: "This week",
          week: "Week"
        },
        headerToolbar: {
          left: "prev,next today",
          center: "title",
          right: "timeGridWeek"
        },
        eventTimeFormat: {
          hour: "numeric",
          minute: "2-digit",
          meridiem: "short"
        },
        initialView: "timeGridWeek",
        allDaySlot: false,
        editable: true,
        eventStartEditable: true,
        eventResizableFromStart: true,
        selectable: true,
        selectMirror: false,
        dayMaxEvents: false,
        weekends: true,
        nowIndicator: true,
        firstDay: 1,
        height: "auto",
        aspectRatio: 4,
        eventAllow: this.allowShiftEditing,
        selectAllow: this.allowShiftEditing,
        eventOverlap: this.allowOverlap,
        selectOverlap: this.allowOverlap,
        events: this.loadEvents,
        select: this.handleDateSelect,
        eventClick: this.handleEventClick,
        eventsSet: this.handleEventsSet,
        eventChange: this.handleEventChange
        /* you can update a remote database when these fire:
        eventAdd:
        eventChange:
        eventRemove:
        */
      },
      calendarEvents: [],
      calendarApi: null,
      selectedCalendarEvent: null
    };
  },
  mounted() {
    this.calendarApi = this.$refs.calendar.getApi();
  },
  methods: {
    allowShiftEditing(dropInfo, draggedEvent) {
      if (!draggedEvent) draggedEvent = dropInfo;
      if (this.allowWeekEdit) {
        return (
          (new Date(dropInfo.start).getTime() > new Date().getTime() &&
            new Date(draggedEvent.start).getTime() > new Date().getTime()) ||
          (this.$toastr.w(
            `Shift modification before today/now is not allowed`
          ) &&
            false)
        );
      } else {
        return (
          (new Date(dropInfo.start).getISOWeek() > new Date().getISOWeek() &&
            new Date(draggedEvent.start).getISOWeek() >
              new Date().getISOWeek()) ||
          (this.$toastr.w(`Shift modification for this week is not allowed`) &&
            false)
        );
      }
    },
    allowOverlap() {
      this.$toastr.w("Overlapping shifts are not allowed");
      return false;
    },
    loadEvents(info, successCallback) {
      CalendarService.getEvents({
        start: info.startStr,
        end: info.endStr
      }).then(evts => {
        successCallback(evts);
      });
    },
    handleDateSelect(eventInfo) {
      // let title = prompt("Please enter a new title for your event");
      let title = "Work";
      CalendarService.saveEvent({
        title: title,
        start: eventInfo.startStr,
        end: eventInfo.endStr
      }).then(() => {
        this.$toastr.s(`Event created successfully`);
        eventInfo.view.calendar.refetchEvents();
      });
    },
    handleEventClick(eventInfo) {
      if (this.allowShiftEditing(eventInfo.event)) {
        this.selectedCalendarEvent = {
          id: eventInfo.event.id,
          title: eventInfo.event.title,
          startStr: eventInfo.event.startStr,
          endStr: eventInfo.event.endStr
        };
        // this.selectedCalendarEvent = eventInfo.event;
        this.showDialog = true;
      }
    },
    updateEvent(event) {
      CalendarService.updateEvent(event).then(() => {
        this.$toastr.s(`Event updated successfully`);
        this.calendarApi.refetchEvents();
        this.showDialog = false;
        this.selectedCalendarEvent = null;
      });
    },
    deleteEvent(event) {
      if (this.confirmDelete(event.title)) {
        CalendarService.deleteEvent(event.id).then(() => {
          this.$toastr.s(`Event deleted successfully`);
          this.calendarApi.refetchEvents();
          this.showDialog = false;
          this.selectedCalendarEvent = null;
        });
      }
    },
    handleEventChange(eventInfo) {
      CalendarService.updateEvent(eventInfo.event).then(() => {
        this.$toastr.s(`Event updated successfully`);
      });
    },
    handleEventsSet(events) {
      if (this.calendarApi) {
        let dailyHours = calculateDailyHours(this.calendarApi, events);
        let weekDayHourTemplate = document.querySelector(
          ".fc-timegrid-slots .total-week-day-hour"
        );
        weekDayHourTemplate && weekDayHourTemplate.remove();
        weekDayHourTemplate = createWeekDayHourTemplate(dailyHours);
        document
          .querySelector(".fc-timegrid-slots")
          .append(weekDayHourTemplate);

        this.weeklyHours = dailyHours
          .reduce((total, evtHrs) => (total += evtHrs), 0);
      }
    },
    closeDialog() {
      this.showDialog = false;
      this.selectedCalendarEvent = null;
    },
    confirmDelete(title) {
      return confirm(`Are you sure you want to delete the event '${title}'`);
    }
  }
};
</script>

<style>
.float-right {
  float: right;
}
.float-left {
  float: left;
}
@media only screen and (max-width: 600px) {
  .full-calendar {
    width: 100%;
    font-size: 12px;
  }
}
.fc-icon {
  line-height: 0.7em !important;
}
</style>