// import { default as http } from "../http-common";
// import { EVENTS_URL, CREATE_URL, UPDATE_URL, DELETE_URL } from "../config";
import { default as data } from "../assets/data/calendar-events.json";

class CalendarService {

  constructor() {
    this.events = data;
    this.maxId = this.events.map(e => e.id).reduce((max, id) => id > max ? id : max, 0);
  }

  async getEvents(options) {
    let dateStart = new Date(options.start);
    let dateEnd = new Date(options.end);
    return new Promise((resolve) => {
      resolve(this.events.filter(e => {
        return new Date(e.start) <= dateEnd && new Date(e.end) >= dateStart;
      }));
    });
    // return http.get(EVENTS_URL, {
    //   params: {
    //     start: options.start,
    //     end: options.end
    //   }
    // });
  }

  async saveEvent(data) {
    // save the entity
    data.id = ++this.maxId;
    this.events.push(data)
    return Promise.resolve(data);
    // return http.post(CREATE_URL, data);
  }

  async updateEvent(data) {
    // update the entity
    let calendarEvent = this.events.find(e => e.id === parseInt(data.id));
    calendarEvent.start = data.start;
    calendarEvent.end = data.end;
    return Promise.resolve(calendarEvent);
    // return http.put(UPDATE_URL + data.id, data);
  }

  async deleteEvent(id) {
    let index = this.events.findIndex(e => e.id === parseInt(id));
    let data = this.events.splice(index, 1);
    return Promise.resolve(data);
    // return http.delete(DELETE_URL + id);
  }
}

export default new CalendarService();
