<template>
  <v-card primary>
    <v-card-title> {{ title }} </v-card-title>
    <v-card-text>
      <v-form ref="form" v-model="valid">
        <v-text-field
          label="Title"
          v-model="eventTitle"
          prepend-icon="mdi-text"
        ></v-text-field>

        <v-row>
          <v-col>
            <v-text-field
              label="Start date"
              v-model="startDate"
              prepend-icon="mdi-calendar"
              type="date"
              :min="minDate"
              :rules="startRules"
            ></v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              label="Start time"
              v-model="startTime"
              type="time"
              :rules="startRules"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row>
          <v-col>
            <v-text-field
              label="End date"
              v-model="endDate"
              prepend-icon="mdi-calendar"
              type="date"
              :min="startDate"
              :rules="endRules"
            ></v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              label="End time"
              v-model="endTime"
              type="time"
              :rules="endRules"
            ></v-text-field>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="primary" @click.stop="updateEvent">Save</v-btn>
      <v-btn color="red" class="white--text" @click.stop="deleteEvent"
        >Delete</v-btn
      >
      <v-btn color="secondary" text @click.stop="close">Cancel</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  model: {
    prop: "event",
    event: "change"
  },
  props: {
    event: Object,
    title: String
  },
  computed: {
    eventTitle() {
      return this.event ? this.event.title : "";
    },
    startStr() {
      return this.event ? this.startDate + "T" + this.startTime : "";
    },
    endStr() {
      return this.event ? this.endDate + "T" + this.endTime : "";
    },
    minDate() {
      return new Date().toISOString().substr(0, 10);
    },
    startRules() {
      const rule = () =>
        new Date(this.startStr).getTime() > new Date().getTime() ||
        "Start date/time must be after today/now.";
      return [rule];
    },
    endRules() {
      const rule = () =>
        new Date(this.endStr).getTime() > new Date(this.startStr).getTime() ||
        "End date/time must be after start date/time.";
      return [rule];
    }
  },
  data: () => ({
    valid: false,
    startDate: "",
    startTime: "",
    endDate: "",
    endTime: ""
  }),
  watch: {
    event(val) {
      this.populateFields(val);
    },
    startStr() {
      this.validateForm();
    },
    endStr() {
      this.validateForm();
    }
  },
  mounted() {
    this.populateFields(this.event);
  },
  methods: {
    populateFields(selectedEvent) {
      if (selectedEvent && selectedEvent.startStr) {
        this.startDate = selectedEvent.startStr.substr(0, 10);
        this.startTime = selectedEvent.startStr.substr(11, 5);
      }
      if (selectedEvent && selectedEvent.endStr) {
        this.endDate = selectedEvent.endStr.substr(0, 10);
        this.endTime = selectedEvent.endStr.substr(11, 5);
      }
    },
    validateForm() {
      this.$refs.form.validate();
    },
    updateEvent() {
      if (this.valid) {
        let calendarEvent = {
          id: this.event.id,
          title: this.event.title,
          startStr: this.startStr,
          start: new Date(this.startStr),
          endSt: this.endStr,
          end: new Date(this.endStr)
        };
        this.$emit("change", calendarEvent);
      }
    },
    deleteEvent() {
      this.$emit("deleteEvent", this.event);
    },
    close() {
      this.$emit("close");
    }
  }
};
</script>

